import logo from "assets/logo.png";
import bg from "assets/bg2.png";
import { Link } from "react-router-dom";
import PATH from "utils/constants/PathConstants";
import { useState } from "react";
import {
  ValidateEmail,
  ValidateFirstName,
  ValidatePassword,
} from "components/Helpers/Validations";
import { setUser } from "Store/Authentication/Authentication.slice";
import { useDispatch } from "react-redux";

const SignUp = () => {
  const dispatch = useDispatch();

  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [firstNameError, setFirstNameError] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");

  const handlePasswordChange = (e) => {
    if (ValidatePassword(e.target.value)) setPasswordError("");
    else setPasswordError("Weak Password !");
    setPassword(e.target.value);
  };
  const handleEmailChange = (e) => {
    if (ValidateEmail(e.target.value)) setEmailError("");
    else setEmailError("Invalid Email !");
    setEmail(e.target.value);
  };
  const handleFirstNameChange = (e) => {
    if (ValidateFirstName(e.target.value)) setFirstNameError("");
    else setFirstNameError("Invalid Name !");
    setFirstName(e.target.value);
  };
  const handleLastNameChange = (e) => {
    setLastName(e.target.value);
  };
  const handleConfirmPasswordChange = (e) => {
    if (password !== e.target.value)
      setConfirmPasswordError("Passwords don't match !");
    else setConfirmPasswordError("");
    setConfirmPassword(e.target.value);
  };

  const handleSignUpEvent = (e) => {
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        email: email,
        firstname: firstName,
        lastName: lastName,
        password: password,
        deviceId: "",
      }),
    };
    fetch("https://api.akool.com/api/v1/public/register", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        if (data.message) setEmailError("Email Already Exists");
        else {
          dispatch(
            setUser({
              user: {
                firstName: firstName,
                lastName: lastName,
                email: email,
                token: data.token,
              },
            })
          );
        }
      });
  };

  return (
    <div className="w-screen py-3 min-h-screen md:h-screen bg-template-cool-blue flex items-center justify-between">
      <img
        src={bg}
        alt="bg"
        className="hidden md:block h-1/2 md:self-end xl:h-full"
      />
      <div className="w-full lg:w-1/2 text-center md:text-left bg-template-grey-blue/50 mx-8 sm:mx-24 md:mr-24 2xl:mr-72 px-4 md:px-16 py-6 rounded-lg shadow flex flex-col items-center">
        <Link to={PATH.HOME}>
          <img src={logo} alt="AKOOL" className="w-56 my-7" />
        </Link>
        <h1 className="text-2xl font-semibold  py-3 uppercase">
          create a new account
        </h1>
        <div className="w-full flex flex-col md:flex-row mt-5">
          <div className="w-full md:w-1/2">
            <label className="py-0 text-white">First Name *</label>
            <input
              type="text"
              value={firstName}
              onChange={(e) => handleFirstNameChange(e)}
              className={`w-full px-5 py-3 mt-1 bg-template-light-blue/50 rounded-lg focus:bg-template-light-grey focus:outline-none ${
                firstNameError ? "border-red-800 border" : ""
              } placeholder-template-dark-blue/60`}
              placeholder="Enter your first name here"
            />
            <p
              className={`text-s font-semibold text-red-900 ${
                firstNameError ? "block" : "hidden"
              }`}
            >
              {firstNameError}
            </p>
          </div>
          <div className="w-full md:w-1/2 md:ml-3">
            <label className="py-0 text-white">Last Name</label>
            <input
              type="text"
              value={lastName}
              onChange={(e) => handleLastNameChange(e)}
              className={`w-full px-5 py-3 mt-1 bg-template-light-blue/50 rounded-lg focus:bg-template-light-grey focus:outline-none placeholder-template-dark-blue/60`}
              placeholder="Enter your last name here"
            />
            <p className={`text-s font-semibold text-red-900 `}></p>
          </div>
        </div>
        <div className="w-full mt-5">
          <label className="py-10 text-white">E-mail *</label>
          <input
            type="text"
            value={email}
            onChange={(e) => handleEmailChange(e)}
            className={`w-full px-5 py-3 mt-1 bg-template-light-blue/50 rounded-lg focus:bg-template-light-grey focus:outline-none ${
              emailError ? "border-red-800 border" : ""
            } placeholder-template-dark-blue/60`}
            placeholder="Enter your email here"
          />
          <p
            className={`text-s font-semibold text-red-900 ${
              emailError ? "block" : "hidden"
            }`}
          >
            {emailError}
          </p>
        </div>
        <div className="w-full flex flex-col md:flex-row mt-5">
          <div className="w-full md:w-1/2">
            <label className="py-0 text-white">Password *</label>
            <input
              type="text"
              value={password}
              onChange={(e) => handlePasswordChange(e)}
              className={`w-full px-5 py-3 mt-1 bg-template-light-blue/50 rounded-lg focus:bg-template-light-grey focus:outline-none ${
                passwordError ? "border-red-800 border" : ""
              } placeholder-template-dark-blue/60`}
              placeholder="Enter your password here"
            />
            <p
              className={`text-s font-semibold text-red-900 ${
                passwordError ? "block" : "hidden"
              } `}
            >
              {passwordError}
            </p>
          </div>
          <div className="w-full md:w-1/2 md:ml-3">
            <label className="py-0 text-white">Confirm Password</label>
            <input
              type="text"
              value={confirmPassword}
              onChange={(e) => handleConfirmPasswordChange(e)}
              className={`w-full px-5 py-3 mt-1 bg-template-light-blue/50 rounded-lg focus:bg-template-light-grey focus:outline-none ${
                confirmPasswordError ? "border-red-900 border" : ""
              } placeholder-template-dark-blue/60`}
              placeholder="Re-write your password"
            />
            <p
              className={`text-s font-semibold text-red-900 ${
                confirmPasswordError ? "" : "hidden"
              }`}
            >
              {confirmPasswordError}
            </p>
          </div>
        </div>

        <button
          className="bg-template-dark-blue text-white w-full rounded p-3 uppercase my-5 hover:opacity-80"
          onClick={handleSignUpEvent}
        >
          Sign up
        </button>
        <p>
          Already Have an account? Log in{" "}
          <Link to={PATH.LOGIN} className="text-template-dark-blue underline">
            Here
          </Link>
        </p>
      </div>
    </div>
  );
};

export default SignUp;
