const Table = ({ headers, body }) => {
  return (
    <table className="shadow-xl w-full">
      <thead>
        <tr className="text-left bg-template-dark-blue text-white">
          {headers.map((h, idx) => (
            <th className="p-2 capitalize" key={idx}>
              {h}
            </th>
          ))}
        </tr>
      </thead>
      <tbody>
        {body.length >= 1 ? (
          body.map((r, idx) => (
            <tr key={idx} className="even:bg-template-grey-blue/5">
              {headers.map((h, i) => (
                <td key={i} className="p-2 last:w-3/5">
                  {r[h]}
                </td>
              ))}
            </tr>
          ))
        ) : (
          <tr>
            <td colSpan={4} className="text-center">
              No data yet
            </td>
          </tr>
        )}
      </tbody>
    </table>
  );
};

export default Table;
