import NavConstants from "utils/constants/NavConstants";
import logo from "assets/logo.png";
import { NavLink, useLocation } from "react-router-dom";
import NavElement from "./NavElement";
import PropTypes from "prop-types";
import { XIcon } from "@heroicons/react/outline";
import UserMenu from "components/Header/UserMenu";
import APIDocsMenu from "components/ScrollMenu/APIDocsMenu";

const Navigation = ({ closeDrawer }) => {
  const isMobile = window.innerWidth <= 768;
  const location = useLocation();
  return (
    <section className="h-screen w-screen fixed z-30 md:w-72 bg-white flex flex-col items-center">
      <XIcon
        onClick={closeDrawer}
        className="w-8 self-end m-2 md:hidden text-template-dark-blue"
      />
      <img
        src={logo}
        alt="akool-logo"
        className="w-9/12 pt-6 md:pt-10 md:pb-5"
      />
      {isMobile ? <UserMenu /> : ""}
      <nav className="w-full" onClick={isMobile ? closeDrawer : () => {}}>
        {NavConstants.map((n, idx) => (
          <div key={n.id}>
            <NavLink key={n.id} to={n.link}>
              {({ isActive }) => (
                <NavElement content={n.content} isActive={isActive} />
              )}
            </NavLink>
            {n.id === 2 && location.pathname === "/docs" ? <APIDocsMenu /> : ""}
          </div>
        ))}
      </nav>
    </section>
  );
};

Navigation.propTypes = {
  closeDrawer: PropTypes.func.isRequired,
};

export default Navigation;
