import { createSlice } from "@reduxjs/toolkit";

export const apiSlice = createSlice({
  name: "apis",
  initialState: {
    apiList: [],
  },
  reducers: {
    setAPIs: (state, action) => {
      state.apiList = action.payload.api;
    },
  },
});

export const { setAPIs } = apiSlice.actions;
export default apiSlice.reducer;
