import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";
import PATH from "utils/constants/PathConstants";

const BreadCrumbs = ({ elementsList }) => {
  return (
    <div className="text-sm flex text-template-grey">
      {elementsList.map((element, id) => (
        <div key={id}>
          <NavLink
            to={element.link}
            className={`hover:underline ${
              id === elementsList.length - 1 ? "font-bold" : ""
            }`}
          >
            {element.content}
          </NavLink>
          <span className="px-2">
            {id === elementsList.length - 1 ? "" : ">"}
          </span>
        </div>
      ))}
    </div>
  );
};

BreadCrumbs.propTypes = {
  elementsList: PropTypes.array,
};

BreadCrumbs.defaultProps = {
  elementsList: [{ content: "Home", link: PATH.HOME }],
};

export default BreadCrumbs;
