import { createSlice } from "@reduxjs/toolkit";
import { CACHED_USER } from "utils/constants/CachedConstant";

export const authSlice = createSlice({
  name: "authentication",
  initialState: {
    loading: false,
    logged: false,
    user: null,
    error: null,
  },
  reducers: {
    setUser: (state, action) => {
      action.payload.user ? (state.logged = true) : (state.logged = false);
      state.user = action.payload.user;
      state.error = action.payload.error;
      localStorage.setItem(
        CACHED_USER,
        JSON.stringify({ user: action.payload.user })
      );
    },
    checkUser: (state) => {
      const data = localStorage.getItem(CACHED_USER);
      const userData = JSON.parse(data);

      userData ? (state.user = userData.user) : (state.user = null);
    },
    startLoading: (state) => {
      state.loading = true;
    },
    stopLoading: (state) => {
      state.loading = false;
    },
  },
});

export const { startLoading, stopLoading, setUser, checkUser } =
  authSlice.actions;
export default authSlice.reducer;
