import { ThumbDownIcon, ThumbUpIcon } from "@heroicons/react/outline";
import {
  ThumbDownIcon as FilledThumbsDown,
  ThumbUpIcon as FilledThumbsUp,
} from "@heroicons/react/solid";
import BreadCrumbs from "components/BreadCrumbs/BreadCrumbs";
import { useState } from "react";

const ComponentHeader = ({ BreadCrumbsList }) => {
  const [helpful, setHelpful] = useState("");
  const handleSetHelpful = (helpfulProp) => {
    setHelpful(helpfulProp);
  };
  return (
    <div className="flex w-full justify-between items-center py-6">
      <div>
        <BreadCrumbs elementsList={BreadCrumbsList} />
      </div>
      <div className="flex items-center justify-end">
        <p className="mr-1 text-sm text-template-grey">Was this helpful?</p>
        {helpful === true ? (
          <FilledThumbsUp className="w-5 text-template-dark-blue mx-1" />
        ) : (
          <ThumbUpIcon
            className="hover:text-template-dark-blue cursor-pointer w-5 text-template-grey mx-1"
            onClick={() => handleSetHelpful(true)}
          />
        )}
        {helpful === false ? (
          <FilledThumbsDown className="w-5 text-template-dark-blue mr-1" />
        ) : (
          <ThumbDownIcon
            className="hover:text-template-dark-blue cursor-pointer w-5 text-template-grey mr-1"
            onClick={() => handleSetHelpful(false)}
          />
        )}
      </div>
    </div>
  );
};

export default ComponentHeader;
