import DOCINFO from "Data/APIDocs.json";
import DocSection from "components/APIDocumentation/DocSection";
import Playground from "components/APIDocumentation/Playground";
import ComponentHeader from "components/Header/ComponentHeader/ComponentHeader";
import PATH from "utils/constants/PathConstants";
import { useSelector } from "react-redux";

const APIDocs = () => {
  const { user } = useSelector((state) => state.authentication);
  const elementsList = [
    { content: "Home", link: PATH.HOME },
    { content: "API Documentation", link: PATH.DOCS },
  ];
  return (
    <div className="w-full flex flex-col md:flex-row md:h-[calc(100vh_-_5rem)]  md:overflow-clip">
      <Playground />
      <div className="h-full md:overflow-scroll w-full md:w-2/3 p-4 md:p-8 flex flex-col mb-24">
        <ComponentHeader BreadCrumbsList={elementsList} />
        <div className="text-template-grey leading-8 w-full">
          <p>
            Your Token: <strong>{user.uid}</strong>
          </p>
          {DOCINFO.docs.map((doc, idx) => (
            <DocSection
              key={idx}
              title={doc.title}
              description={doc.description}
              req_path={doc.req_path}
              request={doc.request}
              req_example={doc.req_example}
              res_body={doc.res_body}
              res_example={doc.res_example}
              note={doc.note}
            />
          ))}
        </div>
      </div>
    </div>
  );
};
export default APIDocs;
