import Header from "components/Header/Header";
import Navigation from "components/navigation/Navigation";
import { useEffect, useState } from "react";

const Layout = ({ children }) => {
  const [drawer, setDrawerOpen] = useState(true);
  const toggleDrawer = () => {
    setDrawerOpen(!drawer);
  };
  useEffect(() => {
    const isMobile = window.innerWidth <= 768;
    setDrawerOpen(!isMobile);
  }, []);
  return (
    <div className="flex">
      {drawer ? <Navigation closeDrawer={toggleDrawer} /> : null}
      <div className="bg-template-light-blue w-full md:w-[calc(100%_-_18rem)] md:ml-72">
        <Header openDrawer={toggleDrawer} />
        <div className="mt-20">{children}</div>
      </div>
    </div>
  );
};

export default Layout;
