import logo from "assets/logo.png";
import { useEffect, useState } from "react";
import PATH from "utils/constants/PathConstants";
import { Link, useNavigate } from "react-router-dom";
import { ValidateEmail } from "components/Helpers/Validations";
import { useDispatch, useSelector } from "react-redux";
import {
  setUser,
  startLoading,
  stopLoading,
} from "Store/Authentication/Authentication.slice";
import { setAPIs } from "Store/apis/apis.slice";

const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.authentication);

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const handleEmailChange = (e) => {
    setEmail(e.target.value);
    if (!ValidateEmail(e.target.value) && e.target.value !== "")
      setEmailError("Invalid Email !");
    else setEmailError("");
  };
  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };
  const handleLoginEvent = (e) => {
    if (!password) {
      setPasswordError("Please Enter a password");
      return;
    }
    dispatch(startLoading());
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        email: email,
        password: password,
        deviceId: "",
      }),
    };
    fetch("https://api.akool.com/api/v1/public/login", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        if (data.user) {
          dispatch(
            setUser({
              user: {
                firstName: data.user.firstName,
                lastName: data.user.lastName,
                email: data.user.email,
                uid: data.user.uid,
              },
            })
          );
          dispatch(stopLoading());
          navigate(PATH.HOME);
        }
      });
  };
  useEffect(() => {
    if (user)
      fetch(`/api/usage/get/${user.uid}`)
        .then((response) => response.json())
        .then((result) => {
          dispatch(setAPIs({ api: result }));
        })
        .catch((error) => console.log("error", error));
  }, [user]);
  return (
    <div className="w-screen h-screen flex items-center justify-center bg-template-light-blue text-template-grey">
      <section className="text-center md:text-left w-4/5 md:w-1/2 xl:w-1/3 p-10 bg-white rounded-xl shadow-lg flex flex-col items-center">
        <Link to={PATH.HOME}>
          <img src={logo} alt="AKOOL" className="w-56 my-7" />
        </Link>
        <h1 className="text-2xl font-semibold  py-3 uppercase">
          Login to your account
        </h1>
        <div className="w-full mt-5">
          <label className="py-10 text-template-grey">User Email</label>
          <input
            type="text"
            value={email}
            onChange={(e) => handleEmailChange(e)}
            className={`w-full px-5 py-3 mt-3 bg-template-light-blue rounded-lg focus:bg-template-light-grey focus:outline-none ${
              emailError ? "border-red-500 border" : ""
            }`}
            placeholder="Enter your email here"
          />
          <p
            className={`text-xs font-semibold text-red-500 ${
              emailError ? "block" : "hidden"
            }`}
          >
            {emailError}
          </p>
        </div>
        <div className="w-full mt-5">
          <label className="py-10 text-template-grey">User Password</label>
          <input
            type="text"
            value={password}
            onChange={(e) => handlePasswordChange(e)}
            className={`w-full px-5 py-3 mt-3 bg-template-light-blue rounded-lg focus:bg-template-light-grey focus:outline-none ${
              passwordError ? "border-red-500 border" : ""
            }`}
            placeholder="Enter your password here"
          />
          <p
            className={`text-xs font-semibold text-red-500 ${
              passwordError ? "block" : "hidden"
            }`}
          >
            {passwordError}
          </p>
        </div>
        <button
          className="bg-template-dark-blue text-white w-full rounded p-3 uppercase my-5 hover:opacity-80"
          onClick={handleLoginEvent}
        >
          Login
        </button>
        <p>
          Don't Have an account? Sign Up{" "}
          <Link to={PATH.SIGNUP} className="text-template-dark-blue underline">
            Here
          </Link>
        </p>
      </section>
    </div>
  );
};

export default Login;
