import Loader from "components/loader/loader";
import ResponseModal from "components/Modal/ResponseModal";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAPIs } from "Store/apis/apis.slice";
import apis from "utils/constants/APIConstants";
import {
  PRICE_OF_1_API,
  STORAGE_AMOUNT_OF_API,
} from "utils/constants/UsageConstant";

const Playground = () => {
  const [domain, setDomain] = useState("sg3.pkool.com");
  const [header, setHeader] = useState("'Content-Type', 'application/json'");
  const [body, setBody] = useState("");
  const [bodyError, setBodyError] = useState("");
  const [api, setApi] = useState("detect");
  const [modalOpen, setModalOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [response, setResponse] = useState("");
  const [responseError, setResponseError] = useState("");

  const { apiList } = useSelector((state) => state.api);
  const { user } = useSelector((state) => state.authentication);

  const dispatch = useDispatch();
  var reqHeaders = new Headers();
  reqHeaders.append("Content-Type", "application/json");

  const createRequest = () => {
    if (!body) {
      setBodyError("Empty Body !");
      return;
    } else {
      var reqOptions = {
        method: "POST",
        headers: reqHeaders,
        body: body,
      };
      setLoading(true);
      fetch(`https://sg3.pkool.com/${api}`, reqOptions)
        .then((response) => response.text())
        .then((result) => {
          setResponse(result);
          setModalOpen(true);
          setLoading(false);
        })
        .catch((error) => {
          setResponseError(error);
          setModalOpen(true);
          setLoading(false);
        });
      incrementAPICount(api);
    }
  };
  useEffect(() => {
    fetch(`/api/usage/get/${user.uid}`)
      .then((response) => response.json())
      .then((result) => {
        dispatch(setAPIs({ api: result }));
      })
      .catch((error) => console.log("error", error));
  }, []);

  const incrementAPICount = (apiName) => {
    const apis = [...apiList];
    const ind = apis.findIndex((ap) => ap.apiRoute === apiName);
    if (ind != -1) {
      const myObj = { ...apis[ind] };
      myObj.numberOfCalls = apis[ind].numberOfCalls + 1;
      myObj.usageAmount = apis[ind].usageAmount + STORAGE_AMOUNT_OF_API;
      myObj.usageCost = apis[ind].usageCost + PRICE_OF_1_API;
      apis[ind] = myObj;
    } else {
      const myObj = {
        apiRoute: apiName,
        numberOfCalls: 1,
        period: Math.floor(new Date().getTime() / 1000),
        usageAmount: STORAGE_AMOUNT_OF_API,
        usageCost: PRICE_OF_1_API,
      };
      apis.push(myObj);
    }
    dispatch(setAPIs({ api: apis }));
    var myBody = {
      userId: user.uid.toString(),
      api: apis,
    };
    var requestOptions = {
      method: "POST",
      body: JSON.stringify(myBody),
      headers: reqHeaders,
    };
    fetch(`/api/usage/set`, requestOptions)
      .then((response) => response.json())
      .then((result) => {
        console.log(result);
      })
      .catch((error) => console.log("error", error));
  };

  const handleBodyChange = (e) => {
    if (bodyError) setBodyError("");
    setBody(e.target.value);
  };
  const handleAPIChange = (e) => {
    setApi(e.target.value);
  };

  const clearBody = () => {
    setBody("");
  };
  return (
    <div className="w-full md:w-1/3 h-full md:overflow-scroll p-8 border-r border-template-grey text-template-grey leading-8">
      <h1 className="text-3xl font-semibold py-5">API Playground</h1>
      <div className="w-full flex flex-col my-4">
        <label>Domain</label>
        <input
          className="p-2 rounded bg-white text-template my-2"
          type="text"
          disabled
          value={domain}
        />
      </div>
      <div className="w-full flex flex-col my-4">
        <label>Header</label>
        <input
          className="p-2 rounded bg-white text-template my-2"
          type="text"
          disabled
          value={header}
        />
      </div>
      <div className="w-full flex flex-col my-4">
        <label>API</label>
        <select
          className="p-4 bg-white pr-7"
          value={api}
          onChange={handleAPIChange}
        >
          {apis.map((apiOption, i) => (
            <option key={i} className="p-4">
              {apiOption}
            </option>
          ))}
        </select>
      </div>
      <div className="w-full flex flex-col my-4">
        <label className="flex">
          Body <p className="text-red-600">*</p>
        </label>
        <div className="w-full flex justify-between text-xs items-center">
          <p>JSON Body</p>
          <div
            className="rounded-md bg-template-royal-blue text-white px-2 py-1 cursor-pointer"
            onClick={clearBody}
          >
            Clear
          </div>
        </div>
        <textarea
          rows="20"
          className={`p-2 rounded bg-white text-template my-2 ${
            bodyError ? "border-red-500 outline-red-500 border-2" : ""
          }`}
          value={body}
          onChange={handleBodyChange}
        ></textarea>
        <p className="text-xs text-red-500">{bodyError}</p>
      </div>
      <button
        className="bg-template-dark-blue rounded text-white p-2 px-5 self-end"
        onClick={createRequest}
      >
        Create POST Request
      </button>
      {loading ? (
        <Loader />
      ) : modalOpen ? (
        <ResponseModal
          data={response}
          error={responseError}
          closeModal={() => setModalOpen(false)}
        />
      ) : (
        ""
      )}
    </div>
  );
};

export default Playground;
