import { ChevronDownIcon } from "@heroicons/react/outline";
import { useState } from "react";
import profileImage from "assets/profile.jpeg";
import { useDispatch, useSelector } from "react-redux";
import { setUser } from "Store/Authentication/Authentication.slice";
import { useNavigate } from "react-router-dom";
import PATH from "utils/constants/PathConstants";

const UserMenu = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const {user} = useSelector((state) => state.authentication)

  const [menuVisible, setMenuVisible] = useState(false);

  const showDropdown = () => {
    setMenuVisible(true);
  };
  const hideDropdown = () => {
    setMenuVisible(false);
  };

  const logoutUser = () => {
    hideDropdown();
    dispatch(setUser({ user: null }));
    navigate(PATH.HOME);
  };

  return (
    <div
      tabIndex={0}
      onFocus={showDropdown}
      onBlur={hideDropdown}
      className="w-full md:w-aut0"
    >
      <div
        className=" flex items-center justify-center md:justify-end cursor-pointer md:hover:bg-template-grey-blue/25 p-3 rounded"
        onClick={showDropdown}
      >
        <img
          src={user.img ? user.img : profileImage}
          alt="profile"
          className="rounded-full w-10 h-10 mr-4"
        />
        <div>
          <div className="text-ellipsis overflow-hidden w-44 font-bold">
            {user.firstName} {user.lastName}
          </div>
          <p className="text-xs opacity-60 text-ellipsis overflow-hidden w-44">
            {user.email}
          </p>
        </div>
        <ChevronDownIcon className="hidden md:block w-6 " />
      </div>
      {menuVisible ? (
        <ul className="fixed right-8 bg-white rounded shadow-lg text-black w-64">
          <li
            className="p-3 hover:bg-template-dark-blue hover:text-white cursor-pointer"
            onClick={logoutUser}
          >
            Logout
          </li>
        </ul>
      ) : null}
    </div>
  );
};
export default UserMenu;
