import ActiveMenuLink from "active-menu-link";
import DOCINFO from "Data/APIDocs.json";

const APIDocsMenu = () => {
  // Not necessary.
  let options = {
    activeClass: "font-bold",
  };

  new ActiveMenuLink(".navbar", options);
  return (
    <nav className="navbar w-full">
      <ul className="navbar-items text-xs pl-10">
        {DOCINFO.docs.map((d, idx) => (
          <li className="w-full" key={idx}>
            <a
              href={`#${d.title.replace(" ", "-")}`}
              className="active:bg-template-royal-blue/5 w-full cursor-pointer "
            >
              <div className="hover:bg-template-royal-blue/5 pt-1 pl-2 hover:border-l border-template-royal-blue hover:font-bold">
                {d.title}
              </div>
            </a>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default APIDocsMenu;
