import Table from "components/Table/Table";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  startLoading,
  stopLoading,
} from "Store/Authentication/Authentication.slice";

const UsageStats = () => {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.authentication);

  const [stats, setStats] = useState([]);
  const headers = ["type", "number of calls", "amount", "price"];
  useEffect(() => {
    dispatch(startLoading());
    fetch(`/api/usage/get/${user.uid}`)
      .then((response) => response.json())
      .then((result) => {
        const body = result.map((r) => {
          return {
            type: r.apiRoute,
            "number of calls": r.numberOfCalls,
            amount: r.usageAmount,
            price: r.usageCost + "$",
          };
        });
        setStats(body);
        dispatch(stopLoading());
      })
      .catch((error) => console.log("error", error));
  }, [dispatch]);

  return (
    <div className="w-full py-8 px-8 mb-24 min-h-[calc(100vh_-_5rem)]">
      <h1 className="text-3xl font-semibold py-5">
        Monthly API and storage usage summary
      </h1>
      <Table headers={headers} body={stats} />
    </div>
  );
};

export default UsageStats;
