import { useScrollSections } from "react-scroll-section";

const SideMenu = () => {
  const sections = useScrollSections();
  return (
    <ul className="w-full overflow">
      {sections.map(({ id, onClick, selected }) => (
        <li
          key={id}
          onClick={onClick}
          selected={selected}
          className={`${
            selected ? "border-l bg-template-royal-blue/5" : ""
          } p-2 border-template-royal-blue cursor-pointer min-w-max`}
        >
          {id.toUpperCase()}
        </li>
      ))}
    </ul>
  );
};

export default SideMenu;
