import { CopyBlock, dracula } from "react-code-blocks";
const CodeComponent = ({ codeBlock }) => {
  return (
    <div className="overflow-hidden">
      <div className="p-3 rounded shadow bg-[#282A36] w-full overflow-scroll md:w-auto">
        <CopyBlock
          language="bash"
          text={codeBlock}
          theme={dracula}
          wrapLines={true}
        />
      </div>
    </div>
  );
};

export default CodeComponent;
